const app = getApp();
import helper from '../common/helper.js';
import pickerAddress from '../components/wangding-pickerAddress.vue';
export default {
	data() {
		return {
			setData: '',
			token: ''
		};
	},
	components: {
		pickerAddress
	},
	onLoad(options) {
		this.token = uni.getStorageSync('token')
		this.options = options
		uni.setNavigationBarTitle({
			title: options.title
		})
	},
	methods: {
		//获取input数据
		onInput(e) {
			this.setData = e.detail.value
		},
		submit() {
			if (this.setData == '') {
				uni.showToast({
					title: '提交值不能为空',
					icon: 'none'
				});
				return false;
			}
			if (this.options.type == 'number') {
				var patrn = /(^[1-9]\d*$)/;
				if (!patrn.test(this.setData)) {
					uni.showToast({
						title: '请输入正整数',
						icon: 'none'
					});
					return ''
				}
			} else if (this.options.type == 'mail') {
				let myreg = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
				if(!myreg.test(this.setData)) {
					uni.showToast({
						title: '请输入正确的邮箱',
						icon: 'none'
					});
				  return false;
				}
			}
			uni.showLoading({
			  title: '提交中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/user/edit`,
				method: 'POST',
				header: {
					authorization: this.token
				},
				data: {
					field: this.options.name,
					value: this.setData
				},
				// data: this.setData,
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res)
					if (res.data.status_code == '1') {
						uni.showToast({
							title: res.data.message
						});
						setTimeout(() => {
							uni.navigateBack({ data: -1 });
						}, 2000)
					} else {
						uni.showToast({
							title: res.data.message
						});
					}
			
				}
			})
		}
	}
};
